<template>
  <div class="pagination">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="page.curPage"
      :page-sizes="[10, 20, 30, 40, 50]"
      :page-size="page.pageSize"
      layout="total,sizes,prev,pager,next,jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
export default {
  name: "pagination",
  inject: ["pagination"], //mixinsprovider
  props: {
    total: {
      //
      type: Number,
      default: 0
    },
    curPage: {
      //
      type: Number,
      default: 1
    },
    pageSize: {
      //
      type: Number,
      default: 10
    }
  },
  watch: {
    pagination: {
      //
      deep: true,
      handler(v) {
        if (!v) {
          return;
        }
        const { pageSize, total, curPage } = v;
        this.curPage = curPage;
        this.pageSize = pageSize;
        this.total = total;
      }
    }
  },
  data() {
    return {
      page: {
        curPage: 1,
        pageSize: 10,
        total: 0
      }
    };
  },
  methods: {
    emitPaginationChangeInfo() {
      //pageChanged
      const pageInfo = {
        pageSize: this.pageSize,
        curPage: this.curPage
      };
      this.$emit("pageChanged", pageInfo);
    },
    handleSizeChange(pageSize) {
      //
      const willMakePageChange = this.total / pageSize + 1 < this.curPage;
      this.pageSize = pageSize;
      //,,
      if (!willMakePageChange) {
        this.emitPaginationChangeInfo();
      }
    },
    handleCurrentChange(curPage) {
      //
      this.curPage = curPage;
      this.emitPaginationChangeInfo();
    }
  }
};
</script>



